img {
	max-width: 25%;
	max-height: 25%;
	min-width: 25%;
	min-height: 25%;
	margin: 0px, 5px, 0px, 5px;
	border-color: #587185;
	border-radius: 20px;
	border-style: solid;
	border-width: 10px;
}

.button {
	width: 10%;
	height: 5%;
	background-color: #587185;
	border: none;
	color: white;
	padding: 16px 32px;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	font-size: 16px;
	margin: 4% 4%;
	cursor: pointer;
	position: relative;
	bottom: 0%;
}

.button:hover {
	background-color: #466b8a;
}
